import request from "../utils/request"; // 获取energylog列表

export var getAllEnergyLog = function getAllEnergyLog(query) {
  return request({
    url: 'get_all_energy_log',
    method: 'get',
    params: query
  });
}; // 删除energylog

export var deleteEnergyLog = function deleteEnergyLog(query) {
  return request({
    url: 'del_energy_log',
    method: 'get',
    params: query
  });
};