import { getAllEnergyLog, deleteEnergyLog } from "../../api/energyLog";
export default {
  name: 'EnergyLogManagement',
  data: function data() {
    return {
      query: {
        username: '',
        pageIndex: 1,
        pageSize: 10
      },
      tableData: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      pageTotal: 0,
      energyLogForm: {},
      idx: -1,
      id: -1,
      titleType: ''
    };
  },
  created: function created() {
    this.getData();
  },
  methods: {
    turnSecond: function turnSecond(time) {
      if (time == 600000) {
        return '10分钟';
      }

      if (time == 0) {
        return '0分钟';
      }

      if (time == 86400000) {
        return '24小时';
      } else {
        return '6小时';
      }
    },
    getData: function getData() {
      var _this = this;

      getAllEnergyLog({
        name: this.query.username,
        page: this.query.pageIndex
      }).then(function (res) {
        // console.log('aaaaaaaaa', res.data);
        if (res.code == 200) {
          _this.tableData = res.data;
          _this.pageTotal = res.total;
        }
      });
    },
    // 触发搜索按钮
    handleSearch: function handleSearch() {
      this.$set(this.query, 'pageIndex', 1);
      this.getData();
    },
