      var _this2 = this;

      console.log(row.id); // 二次确认删除

      this.$confirm('确定要删除吗？', '提示', {
        type: 'warning'
      }).then(function () {
        deleteEnergyLog({
          id: row.id
        }).then(function (res) {
          if (res.code == 200) {
            _this2.$message.success('删除成功');

            _this2.tableData.splice(index, 1);
          }
        });
      }).catch(function () {
        _this2.$message.error('删除失败');
      });
    },
